.container {
  width: 100%;
  height: 2300px;
  background-color: rgb(232, 250, 250);
}
.slogan_bar {
  width: 100%;
  height: 500px;
  background-color: cadetblue;
  display: flex;
  justify-content: center;
  align-items: center;
}
.slogan_bar h2 {
  color: white;
  padding-top: 50px;
  font-size: 60px;
  text-shadow: 1px 1px 10px #000;
}
.tour_table {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 1750px;
  background-color: rgb(219, 248, 190);
}
.table_head {
  cursor: pointer;
  width: 1000px;
  height: 100px;
  margin-bottom: 100px;
  background-color: red;
}
.table_head {
  display: flex;
  width: 100%;
  z-index: 2;
  background-color: pink;
  justify-content: center;
}
.table_head p {
  width: 250px;
  height: 100px;
  background-color: pink;
  display: flex;
  justify-content: center;
  align-items: center;
}
.table_head p:hover {
  background-color: rgb(253, 119, 141);
}
.table_body {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 1350px;
  position: absolute;
  overflow: hidden;
  margin: 200px 0;
  background-color: purple;
}
.table_body > div {
  position: absolute;
  display: flex;
  /* top: -900px; */
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  width: 80%;
  height: auto;
  background-color: bisque;
}
.content {
  margin: 25px;
  width: 400px;
  height: 400px;
  background-color: #f3f3f3;
}
.page_button {
  z-index: 2;
  display: flex;
  justify-content: center;
  background-color: antiquewhite;
  border: 1px solid #000;
}
.page_button p {
  cursor: pointer;
  display: flex;
  width: 50px;
  height: 50px;
  justify-content: center;
  align-items: center;
  background-color: cadetblue;
  border: 1px solid #999;
  color: #fff;
}
.page_button p:first-child {
  background-color: #999;
}
@media (max-width: 1015px) {
  .content {
    width: 200px;
    height: 200px;
  }
}
